import React from "react";
import style from "./personalData.module.scss"
import {Button, TextField} from "@mui/material";
import {useDispatch, useSelector} from "react-redux";
import {FormikErrors, FormikHelpers, useFormik} from "formik";
import {appAC, selectLoading} from "../../../../store/reducers/app.reducer";
import {authAPI} from "../../../../api/auth.api";
import Typography from "@mui/material/Typography";

export interface IValues {
    nickName: string
}

export const PersonalData = () => {
    const isLoading = useSelector(selectLoading);
    const initialValues: IValues = {
        nickName: ""
    };
    const dispatch = useDispatch();
    const onSubmit = async ({nickName}: IValues, formikHelpers: FormikHelpers<IValues>) => {
        try {
            if (nickName) {
                dispatch(appAC.setLoading(true));
                //await authAPI.changePassword(oldPassword, newPassword1);
                //dispatch(appAC.showInfoPopup({type: "success", text: "Password successfully changed"}))
            }

        } catch (e: any) {
            console.error(e.message);
            console.error(e.stack);
        } finally {
            dispatch(appAC.setLoading(false));
            formikHelpers.setSubmitting(false);
        }
    };
    const formik = useFormik({
        initialValues,
        onSubmit
    });


    return (
        <div className={style.personalData}>
            <Typography>Nickname</Typography>
            <form onSubmit={formik.handleSubmit}
                  className={style.form}
            >
                <TextField type="text"
                                 size="small"
                                 fullWidth
                                 variant="outlined"
                                 id="nickName"
                                 {...formik.getFieldProps("oldPassword")}
                                 className={style.field}
                />
                <Button type='submit'
                        color="primary"
                        fullWidth
                        variant="contained"
                        className={style.field}
                        style={{marginTop: "10px"}}
                        disabled={formik.isSubmitting || isLoading}
                >
                    Submit
                </Button>
            </form>
        </div>
    )
}